import { Outlet, Link } from "react-router-dom";
import '../css-manga.css';
import { useLocation } from "react-router-dom";
import { Helmet } from "react-helmet";
import LazyLoad from "react-lazyload";

function Main() {
  const location = useLocation();
  const { pathname } = location;
  const splitlocation = pathname.split('/');
  const link = "Ningen-no-99-Pasento-wa-Hon-de-Dekite-Iru";
  const judul =  link.replace(/-/g, ' ');;
  const subjudul = "99, Ningen no 99% wa Hon de Dekite Iru";
  const author = "Hamita, Hamita";
  const sinopsis = "Ayukawa adalah seorang mahasiswa yang beranggapan bahwa buku segalanya baginya, buku adalah kecerdasan dari umat manusia itu sendiri, sehingga sifat manusia yang berkebalikan dengan buku, membuatnya membenci manusia... tapi, meski dia menyatakan bahwa dirinya membenci manusia, ada seseorang yang dia sukai, itu adalah seorang pustakawan yang dia temu di perpustakaan, karena rasa sukanya itu, dia akhirnya sering pergi ke perpustakaan. Tapi, meski dia sering ke perpustakaan, hubungannya tidak ada kemajuan sama sekali, sehingga dia penasaran apakah ada buku yang dapat membantunya.., dan diapun menemukan sebuah buku tentang ritual.. singkat cerita, dia melakukan ritual yang ada di buku itu, dikatakan bahwa apabila kamu mengharapkan seseorang yang kamu impikan saat melakukan ritual tersebut, orang itu akan muncul di depanmu, tapi, bukannya mengharapkan pustakawan tersebut, dia justru mengharapkan seseorang yang dapat memberinya saran percintaan. Seseorang itu muncul, tapi dia bukan manusia melainkan tsukumogami yang berasal dari buku-buka di rak bukunya... ";
  const statusmanga = "Ongoing";
  
  return (
    <LazyLoad>
    <div className="main">
      <Helmet>
        <title>{judul} - Riztranslation</title>
        <meta name='description' content='Free manga serial, web, oneshot bahasa indonesia' />
        <meta name='author' content='Riztranslation' />
        <meta property="og:title" content="Riztranslation" />
        <meta property="og:description" content="Free manga serial, web, oneshot bahasa indonesia" />
        <meta property="og:url" content="https://riztranslation.dee.al" />
      </Helmet>
      <div className="sampul">
        <img src="https://pbs.twimg.com/media/Fna7705aAAAFafa?format=webp" alt="sampul
        " />
      </div>
      <div className="detail">
        <h3>{judul}</h3>
        <div style={{
          fontSize: '0.7em',
          paddingLeft: '3px',
          transform: 'translateY(-30%)',
          fontFamily: 'poppins, sans-serief'
        }}>{subjudul}</div>
        <h5>{author}</h5>
        <section>
          <a href="/">Romance</a>
          <a href="/">Web Comic</a>
          <a href="/">Comedy</a>
          <a href="/">Slice of Life</a>
        </section>
        <p className="status">Status: {statusmanga}</p>
        <div className="deskripsi">
          <p>{sinopsis}</p>
        </div>
      </div>
      <div className="chapter" id="chapter">
        <ul className="conNav">
          <li className={splitlocation[2] === "Komen" ? "" : splitlocation[2] === "Terkait" ? "" : "on"}>
            <Link className="link" to={'/'+link+'/'}>Chapter</Link>
          </li>
          <li className={splitlocation[2] === "Komen" ? "on" : ""}>
            <Link className="link" to={'/'+link+'/Komen'}>Komen</Link>
          </li>
          <li className={splitlocation[2] === "Terkait" ? "on" : ""}>
            <Link className="link" to={'/'+link+'/Terkait'}>Terkait</Link>
          </li>
        </ul>
        <div style={{ margin: '5px' }}><Outlet /></div>
        <div className="borderbtm" id="borderbtm"></div>
      </div>
    </div>
    </LazyLoad>
  );
}

export default Main;
