import TimeAgo from 'timeago-react';
import { Link, Outlet } from 'react-router-dom';
import React from 'react';
import { KomponenCh } from '../../../A-komponen/Chkomponen';

export const Gomi1 = () => {
    const judul = "Kono Gomi wo Nanto Yobu";
    const link =  judul.replace(/ /g, '-');
    return (
        <div>
            <KomponenCh 
            chapter="1" 
            tanggal=" Mar 13 2023 20:15" 
            vol="sampulCh Gomi" 
            title={judul} 
            linkTitle= {link}
            thumbnile="https://thumbs2.imgbox.com/82/7b/0ZtZTBsb_t.png" />
        </div>
    );
}
