import React from 'react';
import TimeAgo from 'timeago-react';
import { Link, Outlet } from 'react-router-dom';
import "../css-manga.css";

export const KomponenCh = ({chapter, tanggal, vol, title, linkTitle, thumbnile}) => {


    return (
        <div>
            <div className='containerCh'>
                <Link to={"/"+linkTitle}><div className={vol}></div></Link>
                <div>
                    <div className='judul-ch'>{title}</div>
                    <Link className='chapterLink' to={"/"+linkTitle+"/chapter/"+chapter}>
                        <div className='thumbnile-ch'><img src={thumbnile} /></div><div className='chapter-info'><h3>Chapter {chapter}</h3><p><TimeAgo className='timeago'
                        datetime={tanggal}
                        locale='id_ID'
                    /></p></div></Link>
                </div>
            </div>
        </div>
    );
}