import React, { useState } from 'react';
import { Helmet } from 'react-helmet';
import { useLocation } from "react-router-dom";
import { Link, Outlet } from "react-router-dom";
import Giscus from "@giscus/react"
import { FbCmt } from '../komponen/fb-comment';

export const Navigation = () => {
    const location = useLocation();
    const { pathname } = location;
    const splitlocation = pathname.split('/');
    const title = splitlocation[1].replace(/-/g, ' ');
    const klm = document.getElementById('kolom-komen');
    const btn = document.getElementById('btn-komen');
    const cls = document.getElementById('close-komen');
    const dataLink = "https://riztranslation.dee.al/#/"+splitlocation[1]+"/"+splitlocation[2]+"/"+splitlocation[3];

    function open() {
        klm.style.display = 'block';
        btn.style.display = 'none';
        cls.style.display = 'flex';
    };
    function close() {
        klm.style.display = 'none';
        cls.style.display = 'none';
        btn.style.display = 'block';
    };
    const images = document.querySelectorAll('img');
    images.forEach(img => {
        img.loading = 'lazy';
    });
    
    const [status, setStatus] = useState('alt-komen');
    const [on, setOn] = useState('');
    

    return (
        <div>
            <Helmet>
                <title>Chapter {(splitlocation[3])} - {title} - Riztranslation</title>
                <meta name="description" content={"chapter"+splitlocation[3]+" "+"manga"+title} />
                <meta property="og:title" content={title} />
                <meta property="og:description" content={"chapter"+splitlocation[3]+" "+"manga"+title} />
                <style>{`
                   img{
                    height: 100%;
                    width: 100%;
                    display: block;
                    border-top: solid 1px gray;
                    border-bottom: solid 1px gray;
                    min-height: 200px;
                    background-size: contain;
                    background-repeat: no-repeat;
                    background-position: center;
                    background-image: url('https://i.pinimg.com/originals/e1/80/4e/e1804e9f5a5c4893167f45873342faba.gif');
                    background-color: white;
                }
                @keyframes gradient {
                    0% {
                        background-position: 0% 50%;
                    }
                    50% {
                        background-position: 100% 50%;
                    }
                    100% {
                        background-position: 0% 50%;
                    }
                }
                
                .div-mode{
                    display: none;
                }
                .container{
                    display: none;
                }
                button{
                    font-family: 'poppins';
                    padding: 5px;
                    border-radius: 5px;
                    font-size: 0.7em;
                    cursor: pointer;
                    border: none;
                    box-shadow: 1px 1px 5px rgb(47, 42, 42, 0.2);
                }
                .update{
                    height: fit-content;
                }
                .alt-komen{
                    display: none;
                }
                .fb-komento{
                    min-height: 100px;
                }
                `}</style>
            </Helmet>
            <a className='main-front'>
                <div className='first-front'>
                    <Link  className='title-ch prev' to={"/" + (splitlocation[1]) + "/" + (splitlocation[2]) + "/" + (splitlocation[3] * 1 - 1)}></Link>
                    <Link className='title-ch judul' to={"/" + (splitlocation[1])}><p>{title}</p> Ch {(splitlocation[3])}</Link>
                    <Link   className='title-ch next' to={"/" + (splitlocation[1]) + "/" + (splitlocation[2]) + "/" + (splitlocation[3] * 1 + 1)}></Link>
                    <Link onClick={open} id='btn-komen' className='title-ch komen-btn'></Link>
                </div>
                <a onClick={close} id="close-komen" className='close-btn'><p></p></a>
                <div id='kolom-komen' className='second-front' style={{ zIndex: '5', animation: 'fadeIn 0.5s', backdropFilter: 'blur(10px)', paddingTop: '20px', padding: '10px', borderRadius: '3px', height: '85%', backgroundColor: 'rgb(197, 197, 197, 0.5)', margin: '5px', overflow: 'scroll', display: 'none' }}>
                    <button style={{marginTop: '15px'}}>Fb Komen </button>
                    <div className='fb-komento'><FbCmt link={dataLink}/></div>
                    <button onClick={function (){
                        setStatus('alt-komen-on');
                        setOn('');
                    }}>Alternatif Komen {on}</button>
                    <div className={status}><Giscus style={{ animation: 'fadeIn 0.5s', color: 'inherit' }}
                        id="comments"
                        repo="Riztranslation/tgmkch1"
                        repoId="R_kgDOIb5w5A"
                        category="General"
                        categoryId="DIC_kwDOIb5w5M4CSj31"
                        mapping="title"
                        term="Silahkan komen!"
                        reactionsEnabled="1"
                        emitMetadata="1"
                        inputPosition="top"
                        theme="light"
                        lang="id"
                        loading="lazy"
                    /></div>
                </div>
            </a>
        </div>
    );
}