import { Outlet, Link } from "react-router-dom";
import '../css-manga.css';
import { useLocation } from "react-router-dom";
import { Helmet } from "react-helmet";
import LazyLoad from "react-lazyload";

function Main() {
  const location = useLocation();
  const { pathname } = location;
  const splitlocation = pathname.split('/');
  const judul = "Setogiwa ni Todoroku";
  const link =  judul.replace(/ /g, '-');
  const subjudul = "";
  const author = "Kayano Mikura, Kayano Mikura";
  const sinopsis = `Pemenang Penghargaan Manga Pendatang Baru ke-101, Penghargaan Kehormatan... Ketika aku mengutak-atik radio di tengah malam, aku mendengar suara wanita yang tidak kukenal. Suara yang terlontar nyaring itu, Dari semua suara yang pernah aku dengar, Itu paling buruk... Sudah beberapa bulan sejak aku pindah ke kota ini... Aku yang masih belum membiasakan diri, Sering mendengarkan radio. Dan suatu hari, aku menemukan sebuah program dengan pembicaraan kotor. Jingle, iklan, maupun nama programnya tidak ada... Yang aku tahu cuma nama penyiarnya, 'Okura'. Tapi, aku sendiri menantikan program tersebut. Dan, dalam beberapa hari terakhir, aku menyadarinya. Bahwa suara penyiar misterius, 'Okura' dengan teman sekelasku, shinonome amane... Itu sama... Tapi, dia orang yang periang, populer, atletis dan bahkan terpelajar. Kenapa seorang panutan sepertinya melakukan program radio semacam itu... Ini sperti misteri saja.`;
  const statusmanga = "Tamat";
  
  return (
    <LazyLoad>
    <div className="main">
      <Helmet>
        <title>{judul} - Riztranslation</title>
        <meta name='description' content='Free manga serial, web, oneshot bahasa indonesia' />
        <meta name='author' content='Riztranslation' />
        <meta property="og:title" content="Riztranslation" />
        <meta property="og:description" content="Free manga serial, web, oneshot bahasa indonesia" />
        <meta property="og:url" content="https://riztranslation.dee.al" />
      </Helmet>
      <div className="sampul">
        <img src="https://pbs.twimg.com/media/FpdKNMBaYAIK7Is?format=webp" alt="sampul
        " />
      </div>
      <div className="detail">
        <h3>{judul}</h3>
        <div style={{
          fontSize: '0.7em',
          paddingLeft: '3px',
          transform: 'translateY(-30%)',
          fontFamily: 'poppins, sans-serief'
        }}>{subjudul}</div>
        <h5>{author}</h5>
        <section>
          <a href="/">Oneshot</a>
          <a href="/">Drama</a>
          <a href="/">School Life</a>
          <a href="/">Slice of Life</a>
        </section>
        <p className="status">Status: {statusmanga}</p>
        <div className="deskripsi">
          <p>{sinopsis}</p>
        </div>
      </div>
      <div className="chapter" id="chapter">
        <ul className="conNav">
          <li className={splitlocation[2] === "Komen" ? "" : splitlocation[2] === "Terkait" ? "" : "on"}>
            <Link className="link" to={'/'+link+'/'}>Chapter</Link>
          </li>
          <li className={splitlocation[2] === "Komen" ? "on" : ""}>
            <Link className="link" to={'/'+link+'/Komen'}>Komen</Link>
          </li>
          <li className={splitlocation[2] === "Terkait" ? "on" : ""}>
            <Link className="link" to={'/'+link+'/Terkait'}>Terkait</Link>
          </li>
        </ul>
        <div style={{ margin: '5px' }}><Outlet /></div>
        <div className="borderbtm" id="borderbtm"></div>
      </div>
    </div>
    </LazyLoad>
  );
}

export default Main;
