import { Outlet, Link } from "react-router-dom";
import '../css-manga.css';
import { useLocation } from "react-router-dom";
import { Helmet } from "react-helmet";
import LazyLoad from "react-lazyload";

function Main() {
  const location = useLocation();
  const { pathname } = location;
  const splitlocation = pathname.split('/');
  const judul = "Nagasarete Kinjitou";
  const link =  judul.replace(/ /g, '-');
  const subjudul = "";
  const author = "Inazuma Katsura, Inazuma Katsura";
  const sinopsis = `Kehidupan seorang seniman manga yang populer dijungkirbalikkan oleh seorang editor wanita muda cantik! Ketegangan yang tidak bermoral tentang hubungan terlarang dimulai! Kyosuke Takasago, pencipta nama dari duo seniman manga populer "Nankin", yang manga populernya "Taifuu no Hitomi" saat ini sedang diserialisasikan, memiliki sebuah rahasia yang tidak akan pernah ia ceritakan kepada siapa pun di dunia ini, apalagi kepada istrinya, Minami Takasago, yang bertanggung jawab atas karya seni tersebut. Ketika seseorang yang mengetahuinya muncul, dari semua orang, sebagai editor yang bertanggung jawab atas karyanya, kehidupan Kyosuke yang cemerlang mulai runtuh dengan ledakan keras...!`;
  const statusmanga = "Berlangsung";
  
  return (
    <LazyLoad>
    <div className="main">
      <Helmet>
        <title>{judul} - Riztranslation</title>
        <meta name='description' content='Free manga serial, web, oneshot bahasa indonesia' />
        <meta name='author' content='Riztranslation' />
        <meta property="og:title" content="Riztranslation" />
        <meta property="og:description" content="Free manga serial, web, oneshot bahasa indonesia" />
        <meta property="og:url" content="https://riztranslation.dee.al" />
      </Helmet>
      <div className="sampul">
        <img src="https://pbs.twimg.com/media/Ftq2tyvaUAEQ4IX?format=webp" alt="sampul
        " />
      </div>
      <div className="detail">
        <h3>{judul}</h3>
        <div style={{
          fontSize: '0.7em',
          paddingLeft: '3px',
          transform: 'translateY(-30%)',
          fontFamily: 'poppins, sans-serief'
        }}>{subjudul}</div>
        <h5>{author}</h5>
        <section>
          <a href="/">Shounen</a>
          <a href="/">Drama</a>
          <a href="/">Romance</a>
          <a href="/">Slice of Life</a>
        </section>
        <p className="status">Status: {statusmanga}</p>
        <div className="deskripsi">
          <p>{sinopsis}</p>
        </div>
      </div>
      <div className="chapter" id="chapter">
        <ul className="conNav">
          <li className={splitlocation[2] === "Komen" ? "" : splitlocation[2] === "Terkait" ? "" : "on"}>
            <Link className="link" to={'/'+link+'/'}>Chapter</Link>
          </li>
          <li className={splitlocation[2] === "Komen" ? "on" : ""}>
            <Link className="link" to={'/'+link+'/Komen'}>Komen</Link>
          </li>
          <li className={splitlocation[2] === "Terkait" ? "on" : ""}>
            <Link className="link" to={'/'+link+'/Terkait'}>Terkait</Link>
          </li>
        </ul>
        <div style={{ margin: '5px' }}><Outlet /></div>
        <div className="borderbtm" id="borderbtm"></div>
      </div>
    </div>
    </LazyLoad>
  );
}

export default Main;
