export const Navi = () => {
  const location = useLocation();
  const { pathname } = location;
  const splitlocation = pathname.split('/');

  return (
    <div>
      <Navigate to={splitlocation[2] === "chapter" ? splitlocation[1] : splitlocation[0]} />
    </div>
  );
}
