import React from 'react';
import { Helmet } from 'react-helmet';
import { Ch1, Ch2, Ch3, Ch4, Ch5, Ch6, Ch7, Ch8, Ch9, Ch10, Ch11, Ch12, Ch13, Ch14, Ch15, Ch16, Ch17, Ch18 } from '../Manga/Tagahumika/komponen/chKomponen/chapter-komponen';
import { Nzk1 } from '../Manga/Nozokimi/komponen/chKomponen/chapter-komponen';
import { Htb1, Htb2 } from '../Manga/Hina-to-bambi/komponen/chKomponen/chapter-komponen';
import { Htb1raw, Htb2raw } from '../Manga/Hina-to-bambi/komponen/chKomponen/raw-chapter-komponen';
import { TNM1, TNM2, TNM3 } from '../Manga/Tsuka no ma no ichika/komponen/chKomponen/chapter-komponen';
import { Ningen1 } from '../Manga/Manusia-dari-buku/komponen/chKomponen/chapter-komponen';
import { Asuka0 } from '../Manga/asuka/komponen/chKomponen/chapter-komponen';
import { Ashita0 } from '../Manga/mati besok/komponen/chKomponen/chapter-komponen';
import { Naisho0 } from '../Manga/Naisho/komponen/chKomponen/chapter-komponen';
import { Setogiwa0 } from '../Manga/Setogiwa/komponen/chKomponen/chapter-komponen';
import { Gomi2, Gomi3 } from '../Manga/Konogomi/komponen/chKomponen/chapter-komponen';
import { Nagasarete1 } from '../Manga/Nagasarete-Kinjitou/komponen/chKomponen/chapter-komponen';

export const ChapterUp = () => {
    return (
        <div className="update-komponen">
            <Helmet>
                <style>{`
                    .sampulCh {
                        height: 70px;
                        width: 50px;
                        margin-right: 10px;
                        background-size: cover;
                        background-position:center;
                        border: none;
                        background-repeat: no-repeat;
                        border-radius: 3px;
                        display: block;
                    }
                    .judul-ch{
                        display: block;
                        color: inherit;
                        font-size: 13px;
                        height: 15.5px;
                        --max-lines: 1;
                        display: -webkit-box;
                        overflow: hidden;
                        -webkit-box-orient: vertical;
                        -webkit-line-clamp: var(--max-lines);
                        font-family: 'poppins', sans-serif;
                        max-width: 100%;
                        font-weight: 600;
                        margin-bottom: 3px;
                    }
                    .thumbnile-ch{
                        height: 60px;
                        margin-bottom: 5px;
                        width: 100px;
                        background-color:#3f3f3f;
                        border-radius: 3px;
                        grid-area: thumb;
                        display: none;
                      }
                      .chapter-info{
                        grid-area:none;
                        margin: 0px;
                      }
                      .chapterLink{
                        display: block;
                      }
                    `}</style>
            </Helmet>
            <Nagasarete1 />
            <Ch18 />
            <Gomi3 />
            <Gomi2 />
            <Setogiwa0 />
            <Ch17 />
            <Naisho0 />
            <Ashita0 />
            <Asuka0 />
            <Ch16 />
            <Ningen1 />
            <Ch15 />
            <Htb2 />
            <Ch14 />
            <Ch13 />
            <Htb1 />
            <Nzk1 />
            <Ch12 />
            <Ch11 />
            <Ch10 />
            <Ch9 />
            <Ch8 />
            <Ch7 />
            <Ch6 />
            <Ch5 />
            <Ch4 />
            <Ch3 />
            <Ch2 />
            <Ch1 />
        </div>
    );
}

export const RawUp = () => {
    return (
        <div>
            <Helmet>
                <style>{`
                    .sampulCh {
                        height: 80px;
                        width: 55px;
                        margin-right: 10px;
                        background-size: cover;
                        background-position:center;
                        border: none;
                        background-repeat: no-repeat;
                        border-radius: 3px;
                        box-shadow: 0px 0px 49px -19px  gray;
                    }
                `}</style>
            </Helmet>
            <Htb2raw />
            <Htb1raw />
        </div>
    );
};