import TimeAgo from 'timeago-react';
import { Link, Outlet } from 'react-router-dom';
import React from 'react';
import { KomponenCh } from '../../../A-komponen/Chkomponen';

export const Asuka0 = () => {
    const link = "Asuka";
    const judul =  link.replace(/-/g, ' ');;
    return (
        <div>
            <KomponenCh 
            chapter="0" 
            tanggal=" Feb 16 2023 19:10" 
            vol="sampulCh Asuka" 
            title={judul} 
            linkTitle= {link}
            thumbnile="https://thumbs2.imgbox.com/4c/f9/YQdMNUgj_t.png" />
        </div>
    );
}