import TimeAgo from 'timeago-react';
import { Link, Outlet } from 'react-router-dom';
import React from 'react';
import { KomponenCh } from '../../../A-komponen/Chkomponen';

export const Htb2 = () => {
    const link = "Hina-to-Bambi";
    const judul =  link.replace(/-/g, ' ');

    return (
        <div>
            <KomponenCh 
            chapter="2" 
            tanggal="jan 26 2023 21:04"
            vol="sampulCh htbvol1"
            title={judul}
            linkTitle={link}
            thumbnile="https://thumbs2.imgbox.com/8d/01/sy4RTHxB_t.png"/>
        </div>
    );
}
