import { Outlet, Link } from "react-router-dom";
import '../css-manga.css';
import { useLocation } from "react-router-dom";
import { Helmet } from "react-helmet";
import LazyLoad from "react-lazyload";
import { LikeKomponen } from "../../komponen/like-komponen";

function Main() {
  const location = useLocation();
  const { pathname } = location;
  const splitlocation = pathname.split('/');
  const judul = "Tagahumika";
  const subjudul = "Tagafumika, "
  const author = "Hamita, Hamita"

  return (
    <LazyLoad>
      <div className="main">
        <Helmet>
          <title>{(splitlocation[1])} - Riztranslation</title>
          <meta name='description' content='Free manga serial, web, oneshot bahasa indonesia' />
          <meta name='author' content='Riztranslation' />
          <meta property="og:title" content="Riztranslation" />
          <meta property="og:description" content="Free manga serial, web, oneshot bahasa indonesia" />
          <meta property="og:url" content="https://riztranslation.dee.al" />
        </Helmet>
        <div className="sampul">
          <img src="https://pbs.twimg.com/media/FYE77JaaQAEKgyY?format=webp" alt="sampul tagahumika" />
        </div>
        <div className="detail">
          <h3>{judul}</h3>
          <div style={{
            fontSize: '0.7em',
            paddingLeft: '3px',
            transform: 'translateY(-30%)',
            fontFamily: 'poppins, sans-serief'
          }}>{subjudul}</div>
          <h5>{author}</h5>
          <section>
            <a href="/">Romance</a>
            <a href="/">Drama</a>
            <a href="/">Web Comic</a>
            <a href="/">Slice of Life</a>
          </section>
          <p className="status">Status: Ongoing</p>
          <div className="deskripsi">
            <p>Wakui menjalani kehidupan kampus yang membosankan. Satu-satunya kesenangannya adalah korespondensi. Berawal dari sebuah surat yang ia temukan di perpustakaan universitas dan menjadi pusat kehidupan Wakui. Namun, suatu hari, Wakui menemukan sebuah surat yang berisi sesuatu yang tidak seharusnya diketahui oleh korespondennya. Wakui mencurigai bahwa koresponden mungkin adalah kenalannya, dan mulai mencurigai tiga gadis yang terkait dengannya. Namun, ketiga gadis itu agak eksentrik..</p>
          </div>
        </div>
        <div className="chapter" id="chapter">
          <ul className="conNav">
            <li className={splitlocation[2] === "Komen" ? "" : splitlocation[2] === "Terkait" ? "" : "on"}>
              <Link className="link" to='/tagahumika/'>Chapter</Link>
            </li>
            <li className={splitlocation[2] === "Komen" ? "on" : ""}>
              <Link className="link" to='/tagahumika/Komen'>Komen</Link>
            </li>
            <li className={splitlocation[2] === "Terkait" ? "on" : ""}>
              <Link className="link" to='/tagahumika/Terkait'>Terkait</Link>
            </li>
          </ul>
          <div style={{ margin: '5px' }}><Outlet /></div>
          <div className="borderbtm" id="borderbtm"></div>
        </div>
      </div>
    </LazyLoad>
  );
}

export default Main;
